import Vue from 'vue'
import Router from 'vue-router'
import navbar from '@/components/common/navbar'
import aside from '@/components/common/aside'
import index from '@/components/index'
import posts from '@/components/posts'
import postAdd from '@/components/postAdd'
import categories from '@/components/categories'
import comments from '@/components/comments'
import users from '@/components/users'
import navMenus from '@/components/navMenus'
import slides from '@/components/slides'
import settings from '@/components/settings'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      components: {
        default: navbar,
        main: index,
        aside: aside
      }
    }, {
      path: '/posts',
      components: {
        default: navbar,
        main: posts,
        aside: aside
      }
    }, {
      path: '/postAdd',
      components: {
        default: navbar,
        main: postAdd,
        aside: aside
      }
    }, {
      path: '/categories',
      components: {
        default: navbar,
        main: categories,
        aside: aside
      }
    }, {
      path: '/comments',
      components: {
        default: navbar,
        main: comments,
        aside: aside
      }
    }, {
      path: '/users',
      components: {
        default: navbar,
        main: users,
        aside: aside
      }
    }, {
      path: '/navMenus',
      components: {
        default: navbar,
        main: navMenus,
        aside: aside
      }
    }, {
      path: '/slides',
      components: {
        default: navbar,
        main: slides,
        aside: aside
      }
    }, {
      path: '/settings',
      components: {
        default: navbar,
        main: settings,
        aside: aside
      }
    }
  ]
})



